    bottom: 0;
    left: 0;
}
.previousButton{
		position: absolute;
		top: 50%;
		-webkit-transform: translateY(-50%);
		        transform: translateY(-50%);
		z-index: 10;
		background: url(/static/media/left-arrow.c207f596.svg) no-repeat center center / 56px;
		width: 32px;
		height: 32px;
		text-indent: -9999px;
    cursor: pointer;
}
.nextButton{
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 10;
  background: url(/static/media/right-arrow.1ee4c3ee.svg) no-repeat center center / 56px;
  width: 32px;
  height: 32px;
  text-indent: -9999px;
  cursor: pointer;
}
.previousButton:hover{
  background: url(/static/media/left-arrow.c207f596.svg) no-repeat center center / 56px;
}
.nextButton:hover {
  background: url(/static/media/right-arrow.1ee4c3ee.svg) no-repeat center center / 56px;
}
.previousButton {
  left: 0;
  -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
          transform: rotate(180deg) translateY(calc(50% + 0px));
